import React from 'react';
import styled from 'styled-components';

function Checkbox(props) {
  return (
    <div className={ props.className } onClick={ () => props.toggle(props.name) }>
      <input type="checkbox" checked={ props.checked }/>
      { props.text }
      <span onClick={ props.uncheckAll }></span>
    </div>
  );
}

export default styled(Checkbox)`
  position: relative;
  display: block;
  font-size: 13px;
  padding: 8px 15px 8px 45px;
  transition: 0.3s;
  cursor: pointer;
  
  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 15px;
    width: 20px;
    height: 20px;
    background: #ffffff;
    border: 1px solid #d2d5d6;
    border-radius: 3px;
    transform: translateY(-50%);
    transition: 0.3s;
    
    ${props => props.checked && `
      background: #f2fcff no-repeat center / 60%;
      background-image: url(data:image/svg+xml;base64,);
      border-color: #3e9ce8;
    `}
  }
  
  &:hover {
    background: #f1fcff;
    
    span {
      visibility: visible;
      opacity: 1;
    }
  }
    
  span {
    position: absolute;
    top: 50%;
    right: 15px;
    font-size: 11px;
    color: #3e9ce8;
    text-transform: uppercase;
    visibility: hidden;
    opacity: 0;
    transform: translateY(-50%);
    transition: 0.3s;
    
    &:hover {
      text-decoration: underline;
    }
  }
  
  input {
    display: none;
  }
`;


